.charts {
  position: relative;
  top: 104px;
  display: flex;
  flex-wrap: wrap;
}

.charts__date {
  display: flex;
}

.date {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-left: 40px;
  background: white;
}

.date__text {
  font-weight: 300;
  font-size: 16px;
  line-height: 24px;
  align-self: flex-start;
  margin-bottom: 8px;

  color: #1f2961;
  font-family: "Exo 2", "Calibri", sans-serif;
}

.date:nth-child(2) {
  margin-left: 24px;
}

.date__img {
  height: 22px;
  width: 20px;
  right: 50px;
  top: -30px;
  position: relative;
}

.charts__button {
  background: gray;
  box-shadow: 0px 8px 10px 1px rgba(0, 0, 0, 0.14);
  border-radius: 2px;
  width: 141px;
  height: 40px;
  border: none;
  font-weight: 500;
  pointer-events: none;
  font-size: 16px;
  line-height: 24px;
  margin-left: 24px;
  /* identical to box height, or 150% */
  position: relative;
  cursor: pointer;
  top: 32px;

  text-align: center;

  color: #ffffff;
}

@media (max-width: 1000px) {
  .charts__button {
    height: 32px;
    width: 120px;
    margin: 0 auto;
    top: 2px;
    margin-bottom: 18px;
    font-size: 14px;
  }

  .date__text {
    font-weight: 300;
    font-size: 14px;
    line-height: 24px;
    margin-bottom: 4px;
  }

  .wykresy {
    top: 98px;
    flex-direction: column;
  }

  .date {
    margin-left: 16px;
  }

  .date__img {
    top: -27px;
  }

  .date:nth-child(2) {
    margin-left: 24px;
  }
}