.about {
  display: flex;
  flex-direction: column;
  background-color: #3f51b5;
  font-family: "Exo 2", "Calibri", sans-serif;
}

.about__separator {
  height: 132px;
}

.about__content {
  background-size: cover;
  background-image: url("../../Assets/tlo-1.svg");
  background-color: #3f51b5;
}

.about__content__header {
  margin-top: 172px;
  margin-bottom: 128px;
  font-size: 64px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  color: #ffffff;
  text-align: center;
}

.about-brzmi {
  display: flex;
  flex-direction: column;
  background-image: url("../../Assets/tlo-4.svg");
  background-size: cover;
  font-family: 'Exo 2', 'Calibri', sans-serif;
  align-items: center;
  background-color: #f6f6f6;
}

.about-brzmi__header {
  margin-top: 96px;
  margin-bottom: 80px;
  font-size: 64px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  text-align: center;
  color: #3f51b5;
}

.about-brzmi__button {
  text-decoration: none;
  color: white;
  font-size: 18px;
  border: none;
  font-weight: 600;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.33;
  letter-spacing: normal;
  text-align: center;
  color: #ffffff;
  border-radius: 4px;
  background-color: #3f51b5;
  padding-top: 12px;
  padding-bottom: 12px;
  padding-right: 24px;
  padding-left: 24px;
  max-width: 303px;
  margin-bottom: 92px;
}

.about-main {
  background-color: #f6f6f6;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 54px;
  padding-bottom: 112px;
  padding-right: 16px;
  padding-left: 16px;
}

.about-main__description {
  display: flex;
  flex-direction: column;
  max-width: 640px;
  font-family: "Exo 2", "Calibri", sans-serif;
  border-bottom: 1px solid #e2e2e2;
}

.about-main__description__header,
.about-main__authors__header {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-size: 28px;
  font-weight: 600;
  line-height: 1.43;
  color: #3f51b5;
  margin-bottom: 32px;
  margin-top: 64px;
}

.about-main__description__content,
.about-main__authors__content {
  font-size: 18px;
  font-family: "Exo 2", "Calibri", sans-serif;
  line-height: 1.78;
  color: #1f2961;
  margin-bottom: 32px;
}

.about-main__description__img {
  width: 110px;
  height: 32px;
  align-self: flex-end;
  margin-top: 32px;
  margin-bottom: 64px;
}

.about-main__authors {
  display: flex;
  flex-direction: column;
  max-width: 640px;
  font-family: "Exo 2", "Calibri", sans-serif;
}

@media (max-width: 1000px) {
  .about__content__header {
    font-size: 40px;
  }

  .about-main {
    padding-top: 8px;
  }

  .about-main__description__header,
  .about-main__authors__header {
    font-size: 24px;
  }
}

@media (max-width: 800px) {
  .about__separator {
    height: 108px;
  }
}

@media (max-width: 600px) {
  .about__content__header {
    font-size: 36px;
  }

  .about-brzmi__header {
    font-size: 40px;
  }
}