.popup-powietrze__dane {
  color: white;
  font-size: 20px;
  color: #feffff;
  font-family: "Exo 2", "Calibri", sans-serif;
  z-index: 600;
}

.popup__dane__measure-description {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.5;
  color: #1f2961;
  margin-bottom: 8px;
}

.popup__dane-half {
  display: flex;
  flex-direction: column;
  margin-right: 36px;
  margin-bottom: 24px;
}



.popup__dane-half:nth-child(1) a {
  margin-right: 0;
}



.search__input__container {
  position: relative;
}

.search__input-lupa {
  position: absolute;
  top: 15px;
  right: 15px;
}

.search__result {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #1f2961;
  padding: 8px;
  cursor: pointer;
}



.search__input {
  font-size: 14px;
  padding: 10px 20px;
  width: calc(100% - 40px);
  height: 20px;
  border: 1px solid #cacaca96;
  border-radius: 5px;
  color: #000;
}

.search__result:nth-child(odd) {
  background-color: #dce1fa;
  animation: 0.6s ease-in-out slideRight;
}

.search__result:nth-child(even) {
  animation: 0.6s ease-in-out slideLeft;
  background-color: white;
}


@media (max-width: 800px) {
  .search__list {
    max-height: 45vh;
    overflow-y: scroll;
  }

  .popup__dane__measure-description {
    font-size: 12px;
  }

  .popup__dane-flex {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
  }
}

@media (max-width: 450px) {
  .popup-powietrze__dane {
    font-size: 16px;
  }
}

@keyframes slideLeft {
  from {
    transform: translateX(-50px);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;

  }
}

@keyframes slideRight {
  from {
    transform: translateX(50px);
    opacity: 0;
  }

  to {
    transform: translateX(0);
    opacity: 1;

  }
}