.contact-banner {
  display: flex;
  flex-direction: column;
  font-family: 'Exo 2', 'Calibri', sans-serif;
  background-color: #3f51b5;
}

.contact-banner__separator {
  height: 132px;
}

.contact-banner__content {
  background-color: #3f51b5;
  background-size: cover;
  background-image: url("../../Assets/tlo-1.svg");
}

.contact-banner__content__header {
  margin-top: 172px;
  margin-bottom: 128px;
  font-size: 64px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  color: #ffffff;
  text-align: center;
}

.contact-brzmi {
  display: flex;
  flex-direction: column;
  background-image: url("../../Assets/tlo-4.svg");
  background-color: #f6f6f6;
  background-size: cover;
  font-family: 'Exo 2', 'Calibri', sans-serif;
  align-items: center;
}

.contact-brzmi__header {
  margin-top: 160px;
  margin-bottom: 156px;
  font-size: 64px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.25;
  letter-spacing: normal;
  text-align: center;
  color: #3f51b5;
}

.contact-main-bg {
  background-color: #f6f6f6;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 144px;
  padding-bottom: 144px;
}

.contact-main {
  display: flex;
  flex-direction: row;
  max-width: 864px;
  font-family: "Exo 2", "Calibri", sans-serif;
  width: 100%;
  justify-content: space-between;
}

.contact-main__section {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-main__section__upper {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.contact-main__section__upper__icon {
  width: 64px;
  height: 64px;
  margin-bottom: 24px;
}

.contact-main__section__upper__text {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-size: 18px;
  font-weight: 300;
  line-height: 1.78;
  text-align: center;
  color: #1f2961;
}

.contact-info {
  display: flex;
  flex-direction: column;
  margin-top: 32px;
}

.contact-info__description {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-size: 18px;
  font-weight: 500;
  line-height: 2.22;
  text-align: center;
  color: #1f2961;
}

.contact-info__content {
  text-decoration: none;
  color: inherit;
  font-family: "Exo 2", "Calibri", sans-serif;
  font-size: 18px;
  font-weight: bold;
  line-height: 2.22;
  text-align: center;
  color: #3f51b5;
}

@media (max-width: 1000px) {
  .contact-main__section {
    margin-bottom: 32px;
    margin-top: 32px;
  }

  .contact-banner__content__header {
    font-size: 40px;
  }

  .contact-main-bg {
    padding-top: 72px;
    padding-bottom: 72px;
  }

  .contact-main {
    flex-direction: column;

  }
}

@media (max-width: 860px) {
  .contact-banner__content {
    padding-left: 4%;
  }
}

@media (max-width: 800px) {
  .contact-banner__separator {
    height: 108px;
  }
}

@media (max-width: 600px) {
  .contact-banner__content__header {
    font-size: 3
  }

  .contact-brzmi__header {
    font-size: 40px;

  }
}