.map {
  background-color: #3f51b5;
}

.map__menu {
  display: flex;
  position: absolute;
  top: 110px;
  z-index: 450;
  left: 24px;
}

.map__menu__element.active .map__menu__element__link {
  color: #1f2961;
  border-radius: 2px;
  border: 2px solid #3f51b5 !important;
}

.map__menu__element__link {
  width: 100%;
  text-decoration: none;
  color: #1f2961;
  font-size: 16px;
  font-weight: 500;
  font-stretch: normal;
  font-style: normal;
  line-height: 1.5;
  letter-spacing: normal;
  text-align: center;
  font-family: "Exo 2", "Calibri", sans-serif;
  padding: 8px 16px 8px 16px;
  border-radius: 2px;
  box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14);
  background-color: white;
}

.search {
  position: absolute;
  top: 156px;
  z-index: 450;
  left: 24px;
  width: 400px;
  background-color: white;
  color: white;
  padding: 0;
  margin: 8px 0;
  border: none;
  border-radius: 2px;
  box-shadow: 0 -1px 0 0 rgba(0, 0, 0, 0.02), 0 2px 4px 0 rgba(0, 0, 0, 0.2);
  cursor: pointer;
}

.map__scale {
  height: auto;
  width: 216px;
  position: absolute;
  bottom: 22px;
  z-index: 450;
  left: 24px;
}

.dropdown-container {
  cursor: pointer;
  margin-left: 24px;
  border-radius: 2px;
  position: absolute;
  top: 102px;
  z-index: 450;
  left: 228px;
  display: FLEX;
  FLEX-DIRECTION: column;
  align-items: flex-end;
}

.dropdown__description {
  cursor: pointer;
  border: 1px solid #3f51b5;
  padding: 8px 16px;
  font-family: "Exo 2", "Calibri", sans-serif;
  color: white;
  border-radius: 5px;
  display: flex;
  width: 26px;
  background: #3f51b5;
}

.dropdown__description__icon {
  width: 24px;
  height: auto;
}



.dropdown__element {
  display: none;
  cursor: pointer;
  font-family: "Exo 2", "Calibri", sans-serif;
  padding: 12px 10px;
  color: white;
  color: #1f2961;
}

.dropdown__element:nth-child(odd) {
  background-color: white;

}

.dropdown__element:nth-child(even) {
  background-color: #dce1fa;
}

.popup {
  animation-duration: 1s;
  animation-iteration-count: 1;
  display: block;
  background-color: white;
  position: absolute;
  top: 228px;
  z-index: 400;
  left: 24px;
  padding: 16px 12px 24px 16px;
  width: 400px;
}

.popup__dane {
  color: white;
  font-size: 20px;
  color: #feffff;
  font-family: "Exo 2", "Calibri", sans-serif;
}

.popup__dane__place {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-size: 16px;
  font-weight: 600;
  line-height: 1.5;
  color: #3f51b5;
  padding-bottom: 8px;
  border-bottom: 1px solid #c9cee6;
  margin-bottom: 12px;
}

.popup__dane__measure {
  margin-bottom: 8px;

}

.popup__dane__measure__key {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-size: 16px;
  font-weight: 300;
  line-height: 1.5;
  color: #1f2961;
}

.popup__dane__measure__value {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-size: 16px;
  line-height: 1.5;
  color: #1f2961;
}

.popup__wykres {
  font-family: "Exo 2", "Calibri", sans-serif;
  font-size: 16px;
  cursor: pointer;
  line-height: 1.5;
  text-align: right;
  color: #1f2961;
  position: absolute;
  bottom: 24px;
  right: 12px;
}

.popup__close {
  height: auto;
  position: absolute;
  top: 12px;
  right: 12px;
  cursor: pointer;
}

@media (max-width: 1000px) {
  .search {
    width: 300px;
  }
}

@media (min-width: 800px) {
  .popup__dane__place {
    font-size: 12px;
  }

  .popup {
    animation-name: popupAnimation;
  }
}

@media (max-width: 800px) {
  .popup__dane__measure__value {
    font-size: 12px;
  }

  .popup__dane__measure__key {
    font-size: 12px;
  }

  .popup__dane {
    margin-bottom: 24px;

  }

  .dropdown__description {
    padding: 8px;
    display: flex;
    justify-content: flex-end;
  }

  .dropdown-container {
    top: 76px;
    right: 8px;
    left: initial;
  }

  .map__scale {
    width: auto;
    left: 8px;
  }

  .map__menu {
    top: 84px;
    left: 8px;
  }

  .search {
    top: 124px;
    left: 0;
    margin-left: 8px;
    width: calc(100% - 16px);
  }

  .popup {
    animation-name: popupAnimationMobile;
    left: 0;
    top: initial;
    bottom: 0;
    width: 100%;
    z-index: 450;
    border-top-left-radius: 12px;
    border-top-right-radius: 12px;
  }
}

@keyframes popupAnimation {
  0% {
    left: -200px;
    opacity: 0
  }

  100% {
    left: 24px;
    opacity: 1
  }
}

@keyframes popupAnimationMobile {
  0% {
    bottom: 40px;
    opacity: 0
  }

  100% {
    bottom: 0;
    opacity: 1
  }
}